  min-height: 5vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 24px;

  z-index: 3;
  width: 100%;
  background-color: rgb(22, 33, 62);

  position: fixed;
}

.nav-branding {
  height: 3vw;
  display: flex;
  -webkit-filter: brightness(0) invert(1);
          filter: brightness(0) invert(1);
  margin-left: 16px;
  margin-bottom: 5px;
}

.nav-item {
  transition: 0.1s ease;
}

.nav-item:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.navbar img:hover {
  cursor: pointer;
}

@media (max-width: 900px) {
  .nav-item {
    margin: 16px 0;
  }
}

