  .nav-branding {
    height: 40px;
  }
}

.hamburger {
  display: none;
  cursor: pointer;
}

.bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  transition: all 0.3s ease-in-out;
  background-color: rgb(255, 255, 255);
}

@media (max-width: 940px) {
  .hamburger {
    display: block;
  }

  .hamburger.active .bar:nth-child(2) {
    opacity: 0;
  }
  .hamburger.active .bar:nth-child(1) {
    -webkit-transform: translateY(8px) rotate(45deg);
            transform: translateY(8px) rotate(45deg);
  }
  .hamburger.active .bar:nth-child(3) {
    -webkit-transform: translateY(-8px) rotate(-45deg);
            transform: translateY(-8px) rotate(-45deg);
  }
}

.nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 36px;
  gap: 36px;
  margin-right: 36px;
  font-size: 2vmin;
  font-family: elza-narrow, sans-serif;
  font-weight: 300;
}

@media (max-width: 940px) {
  .nav-menu {
    font-size: 16px;
    position: fixed;
    left: -100%;
    top: 5vh;
    flex-direction: column;
    background-color: rgb(22, 33, 62);
    width: 100%;
    text-align: center;
    transition: 0.3s;
  }

  .nav-menu.active {
    position: fixed;
    left: 0;
  }
}

