.action-container{
    height: 60vh;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    position: absolute;
    top: -15%;
    z-index: 1;
}



.title-container{
    height: 60%;
    width: 100%;

    
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;


}

.title-container h1{
    font-size: 6vmax;
    margin: 10px 0 0 0;
    color: white;
    font-family: elza-narrow, sans-serif;
    font-weight: 400;
    font-style: normal;



    text-shadow: 0px 3px 5px rgba(0, 0, 0, 0.7);

}

.title-description{
    height: 20%;
    width: 50%;

    display: flex;
    justify-content: center;
    align-items: center;
}


.title-description p {

    color: white;
    font-size: 2.2vmin;
    font-family: elza-narrow, sans-serif;
    font-weight: 400;
    font-style: normal;
    margin: 0 30px 0 30px;
    
    text-align: center;
    vertical-align: middle;

    text-shadow: 0px 3px 5px rgba(0, 0, 0, 0.7);

    
  }

.title-button-container{
    height: 20%;
    width: 50%;


    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;

    position: relative;
}
 
.button1{
    height: 40%;
    min-width: 35%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.7);
    min-width: 200px;
    margin-top: 20px;

    position: absolute;
    top: 0px;
    z-index: 2000;

}



.button1 p{
    color:rgba(29, 31, 34);
    font-size: 2.2vmin;
    font-family: elza-narrow, sans-serif;
    font-style: normal;
    font-weight: 500;
    
}

@media only screen and (max-width: 750px) {
    .title-container h1{
        font-size: 60px;
        margin: 10px 0 0 0;
    }

    .title-description p {

        font-size: 16px;
        margin: 0 0px 0 0px;
    
        
      }
      .title-description{
        width: 70%;
    }


}

@media only screen and (max-width: 1100px) {
    .action-container{
        top: -15.5%;

    }
}

@media only screen and (max-width: 800px) {
    .action-container{
        top: -15.5%;

    }
}

@media only screen and (max-width: 500px) {
    .title-container h1{
        font-size: 50px;
        margin: 0;
    
    }

    .title-description p {

        font-size: 18px;
        margin: 0 0px 0 0px;
        text-align: left;
        margin-left: 20px;
        line-height:27px;
        
      }
      .title-description{
        width: 90%;
    }

    .button1{
        width: 80%;
        
    
    }

    .button1 p{
        color:rgba(29, 31, 34);
        font-size: 24px;
        font-family: elza-narrow, sans-serif;
        font-weight: 500;
        font-style: normal;
        
    }

    .title-button-container{
        width: 100%;
        height: 25%;

    }

    .title-container{
        height: auto;
        width: 100%;
    
        
        justify-content: center;
        align-items: flex-start;
        margin-left: 80px;
        padding-top: 20px;
    
    
    }

}
